import React from "react";
import { useState, useEffect, useRef } from "react";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Button } from "primereact/button";
import TextInput from "../componets/TextInput";
import { Toolbar } from "primereact/toolbar";
import { InputText } from "primereact/inputtext";
import DropDown from "../componets/DropDown";
import AddPoliticalParty from "../componets/AddPoliticalParty";
import { FilterMatchMode, FilterOperator } from "primereact/api";
import { Toast } from "primereact/toast";
import { Dialog } from "primereact/dialog";
import { TabPanel, TabView } from "primereact/tabview";
import { Image } from "primereact/image";
import PoliticalPartyService from "../service/PoliticalPartyService";
import { FileUpload } from "primereact/fileupload";
import { NET_IP } from "../config/Config";
import axios from "axios";

export const PoliticalPartyManagement = () => {
    const toast = useRef(null);
    const [showAddPartyForm, setShowAddPartyForm] = useState(false);
    const [selectedExecMember, setSelectedExecMember] = useState("");
    const [selectedMember, setSelectedMember] = useState("");
    const [showDialog, setShowDialog] = useState(false);
    const [showEditDialog, setShowEditDialog] = useState(false);
    const [SelectedPoliticalParty, setSelectedPoliticalParty] = useState("");
    var [execMembers, setExecMembers] = useState();
    var [members, setMembers] = useState();
    var [PoliticalParties, setPoliticalParties] = useState([]);
    var [executiveRoles, setExecutiveRoles] = useState([]);
    var [selectedExecutiveRole, setSelectedExecutiveRole] = useState("Select an Executive Role");
    let [data, setData] = useState([]);
    var [load, setLoad] = useState(false);
    var [allFiles, setAllFiles] = useState({ CSV: [] });
    var user = JSON.parse(localStorage.getItem("user"));
    // var [form, setForm] = useState({
    //     File: "",
    //     FileName: "",
    //     PoliticalPartyID: "",
    //     CreatedBy: user?.id ? user.id : 1,
    // });
    var [csvData, setCsvData] = useState([]);
    var [registerdMembers, setRegisterdMembers] = useState();
    var [NonRegisterdMembers, setNonRegisterdMembers] = useState();
    var [execForm, setExecForm] = useState({
        PartyExecutiveRoleID: "",
        RegistrationNumber: "",
        PoliticalPartyID: "",
    });
    var [executiveDetails, setExecutiveDetails] = useState({
        RegistrationNumber: "",
        PoliticalPartyID: "",
    });
    function execRoleHandler(e) {
        setSelectedExecutiveRole(e.value);
        setExecForm({ ...execForm, PartyExecutiveRoleID: e.value.PartyExecutiveRoleID });
    }
    function execMembersHandler() {
        var id = SelectedPoliticalParty?.PoliticalPartyID;
        PoliticalParty.getExecMembersByParty(id).then((data) => {
            setExecMembers(data);
        });
    }
    function membersHandler() {
        var id = SelectedPoliticalParty?.PoliticalPartyID;
        PoliticalParty.getMembersByParty(id).then((data) => {
            setMembers(data);
        });
    }
    function execMemberRoleAvail() {
        var id = SelectedPoliticalParty?.PoliticalPartyID;
        PoliticalParty.getAllExecutiveRoles(id).then((data) => {
            setExecutiveRoles(data);
        });
    }
    function clearExecdata() {
        setSelectedExecutiveRole("Select an Executive Role");
        setData([]);
        setRegistrationNumber("");
    }
    var submittedForm = false;
    function SubmitExecForm() {
        execForm.RegistrationNumber = registrationNumber;
        var newForm = {};
        Object.keys(execForm).map((key) => {
            newForm[key] = execForm[key];
        });
        var error = false;
        let formdata = new FormData();
        var executives = {
            Executives: [newForm],
        };
        if (registrationNumber == "" && selectedExecutiveRole == "Select an Executive Role") {
            return toast.current.show({
                severity: "error",
                summary: "Error Message",
                detail: "Ooops, Please enter a Registration Number and select a Party Executive Role",
                life: 4400,
            });
        }
        if (registrationNumber == "") {
            return toast.current.show({
                severity: "error",
                summary: "Error Message",
                detail: "Ooops, Please enter a Registration Number",
                life: 2000,
            });
        }
        if (selectedExecutiveRole == "Select an Executive Role") {
            return toast.current.show({
                severity: "error",
                summary: "Error Message",
                detail: "Ooops, Please select a Party Executive Role",
                life: 2000,
            });
        }
        var PoliticalParty = new PoliticalPartyService();
        PoliticalParty.addExecutiveMember(executives)
            .then((res) => {
                setTimeout(() => {
                    submittedForm = true;
                    var id = SelectedPoliticalParty?.PoliticalPartyID;
                    PoliticalParty.getExecMembersByParty(id).then((data) => {
                        setExecMembers(data);
                        return toast.current.show({
                            severity: "success",
                            summary: "Success Message",
                            detail: "The Executive member was added successfully",
                            life: 3000,
                        });
                    });
                    setSelectedExecutiveRole("Select an Executive Role");
                    execMemberRoleAvail();
                    setData([]);
                    setRegistrationNumber("");
                }, 1500);
            })
            .catch((e) => {
                submittedForm = false;
                return toast.current.show({
                    severity: "error",
                    summary: "Error Message",
                    detail: "Ooops, The is a technical problem,Please Try Again",
                    life: 2000,
                });
            });
        console.log(newForm);
        if (error == true) {
            toast.current.show({
                severity: "error",
                summary: "Error Message",
                detail: "There was an error",
                life: 3000,
            });
            return false;
        }
    }
    const [registrationNumber, setRegistrationNumber] = useState("");
    const inputFileRef = useRef(null);
    const onBtnClick = () => {
        inputFileRef.current.click();
    };
    var PoliticalParty = new PoliticalPartyService();
    useEffect(() => {
        PoliticalParty.getAllPoliticalParties().then((data) => {
            console.log(data);
            setPoliticalParties(data);
        });
    }, []);

    function getExecData() {
        executiveDetails.RegistrationNumber = registrationNumber;
        executiveDetails.PoliticalPartyID = SelectedPoliticalParty?.PoliticalPartyID;
        var newForm = {};
        Object.keys(executiveDetails).map((key) => {
            newForm[key] = executiveDetails[key];
        });
        if (registrationNumber.length > 0) {
            PoliticalParty.getExecutiveDetails(newForm).then((e) => {
                setData(e?.[0]);
                if (e?.StrResult == "Failed") {
                    toast.current.show({
                        severity: "error",
                        summary: e.StrResult,
                        detail: e.StrMessage,
                        life: 3000,
                    });
                    return false;
                }
                if (e?.[0].StrResult == "Success") {
                    toast.current.show({
                        severity: "success",
                        summary: e?.[0].StrResult,
                        detail: e?.[0].StrMessage,
                        life: 3000,
                    });
                    return false;
                }
                toast.current.show({
                    severity: "error",
                    summary: e?.[0].StrResult,
                    detail: e?.[0].StrMessage,
                    life: 4000,
                });
                return false;
            });
        }

        if (registrationNumber == "") {
            toast.current.show({
                severity: "error",
                summary: "Error Message",
                detail: "Please enter a Registration Number",
                life: 3000,
            });
            return false;
        }
    }

    const [filters, setFilters] = useState({
        global: { value: null, matchMode: FilterMatchMode.CONTAINS },
        name: {
            operator: FilterOperator.AND,
            constraints: [{ value: null, matchMode: FilterMatchMode.STARTS_WITH }],
        },
    });
    const [globalFilterValue, setGlobalFilterValue] = useState("");
    const onGlobalFilterChange = (e) => {
        const value = e.target.value;
        let _filters1 = { ...filters };
        _filters1["global"].value = value;
        setFilters(_filters1);
        setGlobalFilterValue(value);
    };
    function deActivateHandler(id) {
        PoliticalParty.deActivatePoliticalParty(id).then((e) => {
            PoliticalParty.getAllPoliticalParties().then((data) => {
                setPoliticalParties(data);
                return toast.current.show({
                    severity: "success",
                    summary: "Success Message",
                    detail: "Political party de-activated successfully",
                    life: 2000,
                });
            });
        });
    }

    var convertBase64 = (file) => {
        return new Promise((resolve, reject) => {
            const fileReader = new FileReader();
            fileReader.readAsDataURL(file);
            fileReader.onload = () => {
                resolve(fileReader.result);
            };
            fileReader.onerror = (error) => {
                reject(error);
            };
        });
    };
    var handleFiles = (file) => {
        return new Promise((resolve, reject) => {
            const fileReader = new FileReader();
            fileReader.readAsText(file);
            fileReader.onload = () => {
                resolve(fileReader.result);
            };
            fileReader.onerror = (error) => {
                reject(error);
            };
        });
    };
    async function onUploadHandler(e) {
        setLoad(true);
        var files = [];
        var file = e.target.files;
        for (var i = 0; i < file.length; i++) {
            console.log(`file ${i}:`, file[i]);
            const base64 = await convertBase64(file[i]);
            var base64result = base64.split(",")[1];
            console.log(4444, base64result);
            let bodyContent = base64result;
            files.push({
                File: bodyContent,
                FileName: i + ".csv",
                PoliticalPartyID: SelectedPoliticalParty?.PoliticalPartyID,
                CreatedBy: user?.id ? user.id : 1,
            });
        }
        console.log(444, files);
        proccess({ CSV: files });
    }
    async function proccess(files) {
        setLoad(false);
        await checkregisteredMembers(files);
        setAllFiles(files);
    }
    function checkregisteredMembers(files) {
        console.log(files);
        PoliticalParty.verifyRegisteredPartyMembersCsv(files)
            .then((e) => {
                console.log(e);
                var registered = e?.object1.RegisteredVoters;
                console.log("Registered", registered);
                if (registered != null) {
                    setRegisterdMembers(registered);
                }
                if (registered == null) {
                    setRegisterdMembers([]);
                }
                var Nonregistered = e?.object2.NonRegisteredVoters;
                console.log("Not Registered", Nonregistered);
                if (Nonregistered !== null) {
                    setNonRegisterdMembers(Nonregistered);
                }
                if (Nonregistered == null) {
                    setNonRegisterdMembers([]);
                }
            })
            .catch((e) => {
                submittedForm = false;
                return toast.current.show({ severity: "error", summary: "Error Message", detail: "Ooops, The is a technical problem,Please Try Again", life: 3000 });
            });
    }
    function submitUpload() {
        console.log("Testing", allFiles);
        PoliticalParty.addCsvMembers(allFiles)
            .then((e) => {
                setRegisterdMembers([]);
                setNonRegisterdMembers([]);
                setAllFiles({ CSV: [] });
                toast.current.show({
                    severity: "success",
                    summary: "Success",
                    detail: e.StrContent,
                    life: 3000,
                });
                membersHandler();
                return false;
            })
            .catch((e) => {
                submittedForm = false;
                return toast.current.show({ severity: "error", summary: "Error Message", detail: "Ooops, The is a technical problem,Please Try Again", life: 3000 });
            });
    }

    function PartyDetails() {
        return [
            {
                name: "Party name",
                value: SelectedPoliticalParty?.Name,
            },
            {
                name: "Abbreviation",
                value: SelectedPoliticalParty?.Abbreviation,
            },
            {
                name: "Description",
                value: SelectedPoliticalParty?.Description,
            },
            {
                name: "Slogan",
                value: SelectedPoliticalParty?.Slogan,
            },
            {
                name: "Date Registered",
                value: SelectedPoliticalParty?.DateRegistered?.split("T")[0],
            },
            {
                name: "Anniversary",
                value: SelectedPoliticalParty?.Annivesary?.split("T")[0],
            },
            {
                name: "Logo",
                value: <Image preview={true} src={`data:image/png;base64,${SelectedPoliticalParty?.Logo}`} template="Logo" alt="Logo" width="100px" style={{ width: "100px", objectFit: "cover" }} />,
            },
        ];
    }

    const header = (
        <div className="flex flex-column md:flex-row md:justify-content-between md:align-items-center">
            {" "}
            <h5> Political Parties</h5>
        </div>
    );
    return (
        <div className="card  p-align-stretch vertical-container">
            <Toast ref={toast} />
            <div className="">
                <Toolbar
                    className="mb-4"
                    left={
                        <div>
                            <Button className="p-button-success mr-2" icon="pi pi-plus" label="Add Party" onClick={(e) => setShowAddPartyForm(true)} />
                        </div>
                    }
                    right={
                        <div>
                            <span className="block mt-2 md:mt-0 p-input-icon-left">
                                <i className="pi pi-search" />
                                <InputText value={globalFilterValue} onChange={onGlobalFilterChange} placeholder="Search a Political Party" />
                            </span>
                        </div>
                    }
                ></Toolbar>
            </div>
            <Dialog
                draggable={false}
                header={<span>{`Political Party Details - ${SelectedPoliticalParty?.Name}`}</span>}
                style={{ width: "90%", height: "90%" }}
                modal
                visible={showEditDialog}
                onShow={() => {
                    execMembersHandler();
                    membersHandler();
                    execMemberRoleAvail();
                    setExecForm({ ...execForm, PoliticalPartyID: SelectedPoliticalParty?.PoliticalPartyID });
                }}
                onHide={(e) => {
                    setShowEditDialog(false);
                    setRegisterdMembers([]);
                    setNonRegisterdMembers([]);
                }}
            >
                <TabView>
                    <TabPanel header="Party Details">
                        <DataTable size="small" scrollable={true} value={PartyDetails()} dataKey="id" responsiveLayout="scroll" resizableColumns>
                            <Column style={{ width: "100px" }} body={(e) => <b>{e.name}</b>}></Column>
                            <Column body={(e) => e.value}></Column>
                        </DataTable>
                    </TabPanel>
                    <TabPanel header={"Executive Members - " + execMembers?.length}>
                        {data?.StrResult == "Success" || data?.StrResult == "Warning" ? (
                            <div className="grid">
                                <div className="col-16  lg:col-3">
                                    <div style={{ visibility: "hidden" }}>Search</div>
                                    <InputText placeholder="Registration Number" value={registrationNumber} onInput={(e) => setRegistrationNumber(e.target.value)} style={{ width: "100%" }} />
                                </div>
                                <div className="col-16  lg:col-3">
                                    <TextInput label="First name" value={data?.Firstname} disabled />
                                </div>
                                <div className="col-16 lg:col-3">
                                    <TextInput label="Surname" value={data?.Surname} disabled />
                                </div>
                                <div className="col-16  lg:col-3">
                                    <DropDown options={executiveRoles} optionLabel="Name" value={selectedExecutiveRole} onChange={execRoleHandler} label="Party Executive Role" />
                                </div>
                            </div>
                        ) : (
                            <div className="grid">
                                <div className="col-16  lg:col-3">
                                    <div style={{ visibility: "hidden" }}>Search</div>
                                    <InputText placeholder="Registration Number" value={registrationNumber} onInput={(e) => setRegistrationNumber(e.target.value)} style={{ width: "100%" }} />
                                </div>
                            </div>
                        )}
                        {selectedExecutiveRole != "Select an Executive Role" && data?.Firstname != null && (
                            <div className="grid">
                                <div className="col-6  lg:col-2">
                                    <Button onClick={SubmitExecForm} label="Add as Excecutive Member" className="p-button-success" icon="pi pi-plus" type="submit" style={{ width: 250 }} />
                                </div>
                                <div className="col-6  lg:col-4">
                                    <Button onClick={clearExecdata} label="Clear" style={{ marginLeft: 35 }}></Button>
                                </div>
                            </div>
                        )}
                        {selectedExecutiveRole == "Select an Executive Role" && (
                            <div>
                                {registrationNumber != "" ? (
                                    <div className="grid">
                                        <div className="col-4  lg:col-1">
                                            <Button onClick={getExecData} className="p-button-success" icon="pi pi-search" label="Search"></Button>
                                        </div>
                                        <div className="col-4  lg:col-1">
                                            <Button onClick={clearExecdata} label="Clear"></Button>
                                        </div>
                                    </div>
                                ) : (
                                    <div className="grid">
                                        <div className="col-5  lg:col-1">
                                            <Button disabled onClick={getExecData} className="p-button-success" icon="pi pi-search" label="Search"></Button>
                                        </div>
                                        <div className="col-5  lg:col-1">
                                            <Button disabled onClick={clearExecdata} label="Clear"></Button>
                                        </div>
                                    </div>
                                )}
                            </div>
                        )}
                        <div style={{ visibility: "hidden" }}>Space</div>
                        <DataTable
                            size="small"
                            scrollable={true}
                            value={execMembers}
                            dataKey="id"
                            className="datatable-responsive"
                            currentPageReportTemplate="Showing {first} to {last} of {totalRecords} Members"
                            emptyMessage="No Executive Members found."
                            responsiveLayout="scroll"
                            selection={selectedExecMember}
                            onSelectionChange={(e) => setSelectedExecMember(e.value)}
                            resizableColumns
                            columnResizeMode="expand"
                            filters={filters}
                            filterDisplay="Name"
                            globalFilterFields={["Name"]}
                        >
                            <Column field="Name" header="Name"></Column>
                            <Column field="Surname" header="Surname"></Column>
                            <Column field="Role" header="Role"></Column>
                        </DataTable>
                    </TabPanel>
                    <TabPanel header={" Members - " + members?.length}>
                        {allFiles.CSV?.length == 0 ? (
                            <div>
                                <div className="col">
                                    <label htmlFor="description">Upload CSV files</label>
                                    <div style={{ visibility: "hidden" }}>Space</div>
                                    <React.Fragment>
                                        <Button label="Select files" onClick={onBtnClick} className="p-button-success mr-3" icon={"pi pi-plus"} />
                                        <input ref={inputFileRef} type={"file"} multiple onChange={(e) => onUploadHandler(e)} style={{ display: "none" }}></input>
                                        {allFiles.CSV?.length > 0 && <Button onClick={submitUpload} label="Add all Registered Members" icon="pi pi-plus" type="submit" />}
                                    </React.Fragment>
                                </div>
                                <div style={{ visibility: "hidden" }}>Space</div>
                                <DataTable
                                    size="small"
                                    scrollable={true}
                                    value={members}
                                    dataKey="id"
                                    paginator
                                    rows={10}
                                    rowsPerPageOptions={[10, 20]}
                                    className="datatable-responsive"
                                    currentPageReportTemplate="Showing {first} to {last} of {totalRecords} Members"
                                    emptyMessage="No members found."
                                    responsiveLayout="scroll"
                                    selection={selectedMember}
                                    onSelectionChange={(e) => setSelectedMember(e.value)}
                                    resizableColumns
                                    columnResizeMode="expand"
                                    filters={filters}
                                    filterDisplay="Name"
                                    globalFilterFields={["Name"]}
                                >
                                    <Column field="Name" header="Name" sortable></Column>
                                    <Column field="Surname" header="Surname" sortable></Column>
                                    <Column field="RegistrationNumber" header="Registration Number" sortable></Column>
                                </DataTable>
                                <div style={{ visibility: "hidden" }}>Space</div>
                            </div>
                        ) : (
                            <div className="col">
                                <label htmlFor="description">Upload CSV files</label>
                                <div style={{ visibility: "hidden" }}>Space</div>
                                <React.Fragment>
                                    <Button label="Select files" onClick={onBtnClick} className="p-button-success mr-3" icon={"pi pi-plus"} />
                                    <input ref={inputFileRef} type={"file"} multiple onChange={(e) => onUploadHandler(e)} style={{ display: "none" }}></input>
                                    {allFiles.CSV?.length > 0 && <Button onClick={submitUpload} label="Add all Registered Members" icon="pi pi-plus" type="submit" />}
                                </React.Fragment>
                                <div style={{ visibility: "hidden" }}>Space</div>
                                <TabView>
                                    <TabPanel header="Registered">
                                        <DataTable
                                            size="small"
                                            scrollable={true}
                                            scrollHeight="400px"
                                            value={registerdMembers}
                                            dataKey="id"
                                            loading={load}
                                            rowsPerPageOptions={[5, 10, 25]}
                                            className="datatable-responsive"
                                            currentPageReportTemplate="Showing {first} to {last} of {totalRecords} Members"
                                            emptyMessage="No members found."
                                            responsiveLayout="scroll"
                                            selection={selectedMember}
                                            onSelectionChange={(e) => setSelectedMember(e.value)}
                                            resizableColumns
                                            columnResizeMode="expand"
                                            filters={filters}
                                            filterDisplay="Name"
                                            globalFilterFields={["Name"]}
                                        >
                                            <Column field="Name" header="Name" sortable></Column>
                                            <Column field="Surname" header="Surname" sortable></Column>
                                            <Column field="RegistrationNumber" header="Registration Number" sortable></Column>
                                        </DataTable>
                                    </TabPanel>
                                    <TabPanel header="Not Registered">
                                        <DataTable
                                            size="small"
                                            scrollable={true}
                                            scrollHeight="400px"
                                            value={NonRegisterdMembers}
                                            loading={load}
                                            rowsPerPageOptions={[5, 10, 25]}
                                            className="datatable-responsive"
                                            currentPageReportTemplate="Showing {first} to {last} of {totalRecords} Members"
                                            emptyMessage="No members found."
                                            responsiveLayout="scroll"
                                            selection={selectedMember}
                                            onSelectionChange={(e) => setSelectedMember(e.value)}
                                            resizableColumns
                                            columnResizeMode="expand"
                                            filters={filters}
                                            filterDisplay="Name"
                                            globalFilterFields={["Name"]}
                                        >
                                            <Column field="Name" header="Name" sortable></Column>
                                            <Column field="Surname" header="Surname" sortable></Column>
                                            <Column field="RegistrationNumber" header="Registration Number" sortable></Column>
                                        </DataTable>
                                    </TabPanel>
                                </TabView>
                            </div>
                        )}
                    </TabPanel>
                </TabView>
            </Dialog>
            <Dialog
                draggable={false}
                header={<span>{`Political Party Details - ${SelectedPoliticalParty?.Name} `}</span>}
                style={{ width: "90%", height: "90%" }}
                modal
                visible={showDialog}
                onShow={() => {
                    execMembersHandler();
                    membersHandler();
                    execMemberRoleAvail();
                }}
                onHide={(e) => {
                    setShowDialog(false);
                }}
            >
                <TabView>
                    <TabPanel header="Party Details">
                        <DataTable size="small" scrollable={true} value={PartyDetails()} dataKey="id" responsiveLayout="scroll" resizableColumns>
                            <Column style={{ width: "100px" }} body={(e) => <b>{e.name}</b>}></Column>
                            <Column body={(e) => e.value}></Column>
                        </DataTable>
                    </TabPanel>
                    <TabPanel header={"Executive Members - " + execMembers?.length}>
                        <DataTable
                            size="small"
                            scrollable={true}
                            value={execMembers}
                            dataKey="id"
                            className="datatable-responsive"
                            currentPageReportTemplate="Showing {first} to {last} of {totalRecords} Members"
                            emptyMessage="No Executive Members found."
                            responsiveLayout="scroll"
                            selection={selectedExecMember}
                            onSelectionChange={(e) => setSelectedExecMember(e.value)}
                            resizableColumns
                            columnResizeMode="expand"
                            filters={filters}
                            filterDisplay="Name"
                            globalFilterFields={["Name"]}
                        >
                            <Column field="Name" header="Name"></Column>
                            <Column field="Surname" header="Surname"></Column>
                            <Column field="Role" header="Role"></Column>
                        </DataTable>
                    </TabPanel>
                    <TabPanel header={"Members - " + members?.length}>
                        <DataTable
                            size="small"
                            scrollable={true}
                            value={members}
                            dataKey="id"
                            paginator
                            rows={10}
                            rowsPerPageOptions={[10, 20]}
                            className="datatable-responsive"
                            currentPageReportTemplate="Showing {first} to {last} of {totalRecords} Members"
                            emptyMessage="No members found."
                            responsiveLayout="scroll"
                            selection={selectedMember}
                            onSelectionChange={(e) => setSelectedMember(e.value)}
                            resizableColumns
                            columnResizeMode="expand"
                            filters={filters}
                            filterDisplay="Name"
                            globalFilterFields={["Name"]}
                        >
                            <Column field="Name" header="Name" sortable></Column>
                            <Column field="Surname" header="Surname" sortable></Column>
                            <Column field="RegistrationNumber" header="Registration Number" sortable></Column>
                        </DataTable>
                    </TabPanel>
                </TabView>
            </Dialog>

            <AddPoliticalParty setPoliticalParties={setPoliticalParties} show={showAddPartyForm} setShow={setShowAddPartyForm} />

            <DataTable
                size="small"
                scrollable={true}
                value={PoliticalParties}
                dataKey="id"
                paginator
                rows={5}
                rowsPerPageOptions={[5, 10, 25]}
                className="datatable-responsive"
                currentPageReportTemplate="Showing {first} to {last} of {totalRecords} political parties"
                emptyMessage="No political party found."
                header={header}
                responsiveLayout="scroll"
                selection={SelectedPoliticalParty}
                onSelectionChange={(e) => setSelectedPoliticalParty(e.value)}
                resizableColumns
                columnResizeMode="expand"
                filters={filters}
                filterDisplay="Name"
                globalFilterFields={["Name"]}
            >
                <Column field="Name" header="Name" sortable></Column>
                <Column field="Abbreviation" header="Abbreviation" sortable></Column>
                <Column field="Description" header="Description"></Column>
                <Column field="Slogan" header="Slogan"></Column>
                <Column field="Logo" header="Logo" body={(e) => <Image preview={true} src={`data:image/png;base64,${e.Logo}`} template="Logo" alt="Logo" width="100px" style={{ width: "100px", objectFit: "cover" }} />} headerStyle={{ width: "14%", minWidth: "10rem" }}></Column>
                <Column field="DateRegistered" header="Date Registered" body={(e) => e?.DateRegistered?.split("T")[0]}></Column>
                <Column field="Annivesary" header="Anniversary" body={(e) => e?.Annivesary?.split("T")[0]}></Column>
                <Column
                    field="Status"
                    header="Status"
                    body={(e) =>
                        parseInt(e.Status) == 1 ? <Button label="Active" style={{ textAlign: "center", height: "30px" }} className="p-button-success p-button-rounded" /> : <Button label="Not Active" style={{ textAlign: "center", height: "30px" }} className="p-button-danger p-button-rounded" />
                    }
                ></Column>
                <Column
                    field="actions"
                    header="Actions"
                    body={(e) => (
                        <>
                            <Button
                                style={{ textAlign: "center", width: "30px", height: "30px" }}
                                icon={"pi pi-pencil"}
                                className="p-button-primary p-button-rounded mr-2 "
                                tooltip="Click to Edit"
                                onClick={(a) => {
                                    setShowEditDialog(true);
                                    setSelectedPoliticalParty(e);
                                }}
                            />
                            <Button
                                style={{
                                    textAlign: "center",
                                    width: "30px",
                                    height: "30px",
                                }}
                                icon={"pi pi-eye"}
                                tooltipOptions={{ position: "top" }}
                                className="p-button-primary p-button-rounded mr-2"
                                tooltip="Click to View"
                                onClick={(a) => {
                                    setShowDialog(true);
                                    setSelectedPoliticalParty(e);
                                }}
                            />
                            {parseInt(e.Status) == 1 ? (
                                <Button
                                    onClick={(a) => {
                                        deActivateHandler(e.PoliticalPartyID);
                                    }}
                                    style={{ textAlign: "center", width: "30px", height: "30px" }}
                                    icon={"pi pi-times"}
                                    className="p-button-danger p-button-rounded mr-2"
                                    tooltip="Click to De-Activate"
                                />
                            ) : (
                                <Button disabled style={{ textAlign: "center", width: "30px", height: "30px" }} icon={"pi pi- pi-times"} className="p-button-danger p-button-rounded mr-2" tooltip="Click to Activate" />
                            )}
                        </>
                    )}
                ></Column>
            </DataTable>
        </div>
    );
};
